// ÐŸÑ€Ð¸Ð½Ð¸Ð¼Ð°ÐµÑ‚ ÐºÐ»ÑŽÑ‡ `key` Ð¿Ð¾ ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼Ñƒ Ð±ÑƒÐ´ÐµÑ‚ Ð¿Ñ€Ð¾Ð¸Ð·Ð²ÐµÐ´ÐµÐ½Ð° Ð²Ñ‹Ð±Ð¾Ñ€ÐºÐ°.
const load = key => {
  try {
    const localData = localStorage.getItem(key);
    localStorage.getItem('last-tab');

    return localData === null ? undefined : JSON.parse(localData);
  } catch (err) {
    console.error('Get state error: ', err);
  }
};

// ÐŸÑ€Ð¸Ð½Ð¸Ð¼Ð°ÐµÑ‚ ÐºÐ»ÑŽÑ‡ `key` Ð¸ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ `value`.
const save = (key, value) => {
  try {
    const localData = JSON.stringify(value);
    localStorage.setItem(key, localData);
  } catch (err) {
    console.error('Set state error: ', err);
  }
};

// ÐŸÑ€Ð¸Ð½Ð¸Ð¼Ð°ÐµÑ‚ ÐºÐ»ÑŽÑ‡ `key` Ð¸ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ `value`.
const remove = (key, value) => {
  const localData = load(key);
  localData.splice(localData.indexOf(value), 1);
  save(key, localData);
};

export default { load, save, remove };
